.intro {
  background: $intro-background;
  color: $color-default-white;
  padding-top: 52px;

  @include vp-1199 {
    padding-top: 82px;
  }

  @include vp-767 {
    padding-top: 50px;
  }
}

.intro__container {

  align-items: center;
  padding-bottom: 51px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

  @include vp-1199 {
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    padding-bottom: 82px;
  }

  @include vp-767 {
    display: block;
    padding-bottom: 80px;
  }
}

// intro__info

.intro__info {
  display: flex;
  flex-direction: column;
  align-items: end;
  text-align: right;

  margin-top: 67px;
  padding-left: 98px;
  margin-right: 64px;

  @include vp-1199 {
    margin-top: 23px;
    padding-left: 80px;
    margin-right: 0;
  }

  @include vp-767 {
    padding-left: 0;
    margin-top: 59px;
  }
}

.intro__title {
  position: relative;
  margin: 0 0 54px;

  @include vp-1199 {
    margin: 0 0 40px;
  }

  @include vp-767 {
    margin: 0 0 43px;
  }
}

.intro__info h1 {
  font-family: $ff-placeholder-second;
  font-size: 109px;
  font-weight: 700;
  line-height: 109px;
  margin: 0;

  letter-spacing: 0.03em;
  max-width: 298px;

  @include vp-1199 {
    font-size: 86px;
    line-height: 86px;
  }

  @include vp-767 {
    font-size: 78px;
    line-height: 79px;
  }
}

.intro__info span {
  font-family: $ff-placeholder-second;
  font-size: 49px;
  font-weight: 400;
  line-height: 59px;
  letter-spacing: 0.03em;

  order: -1;
  margin-bottom: 5px;
  max-width: 280px;

  @include vp-1199 {
    font-size: 34px;
    line-height: 41px;
  }

  @include vp-767 {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 8px;
  }
}

.intro__link {
  @include vp-767 {
    align-self: stretch;
  }
}

// Ссылка на секцию с абонементами
.intro__link a {
  display: inline-block;
  vertical-align: middle;
  font-family: $ff-placeholder;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  user-select: none;

  padding: 19px 54px;
  max-width: 237px;
  border: 4px solid $color-default-white;
  align-self: flex-end;

  transition: all 0.3s ease;

  box-shadow: $btn-shadow-intro;

  @include vp-1199 {
    width: 183px;
    padding: 19px 8px;
    text-align: center;
  }

  @include vp-767 {
    width: 100%;
    max-width: none;
  }

  @include effects-off {
    &:hover,
    &:focus {
      color: $color-crimson;
      background-color: $color-ghost-white;
    }

    &:active {
      color: $color-dodger-blue;
      box-shadow: $btn-shadow-intro--active;
    }
  }

  &:active {
    color: $color-deep-sky-blue;
    background-color: $color-default-white;
  }
}

// intro__image
.intro__image {
  width: 500px;
  height: 556px;
  background-color: $color-cobalt;

  align-self: start;

  @include vp-1199 {
    width: 324px;
    height: 390px;
    margin-right: 0;
    flex-shrink: 0;
  }

  @include vp-767 {
    display: none;
  }
}

.intro__image img {
  width: 100%;
  height: 100%;
}
