.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  padding-top: 30px;

  @include vp-1199 {
    padding-bottom: 17px;
    padding-top: 38px;
  }

  @include vp-767 {
    padding-top: 19px;
  }
}

.header__block {
  display: flex;
  justify-content: space-between;
}

.header__left-block {
  display: flex;
  align-items: center;
}

/* Чтобы при добавлении контента логотип не уменьшался и ничего не залазило на него  */
.header__logo {
  flex-shrink: 0;
  margin-right: 100px;
}

.header__block a {
  font-family: $ff-placeholder-second;
  font-size: 26px;
  line-height: 26px;
  font-weight: 700;
  letter-spacing: 0.03em;
  color: $color-default-white;
  user-select: none;

  transition:
    letter-spacing $trans-default,
    color $trans-default,
    text-shadow $trans-default-1;

  @include vp-1199 {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }


  @include effects-off {
    &:hover,
    &:focus {
      letter-spacing: 1.8px;
      color: $color-deep-sky-blue;
      text-shadow: 0px -4px 20px $color-deep-sky-blue;
    }

    &:active {
      color: $color-purple-heart;
    }
  }

  &:active {
    color: $color-blue-gem;
  }
}
